@import '../../variables';
.arrow {
    height: 50px;
    width: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #f9f9f9;
    border-radius: 50%;
    cursor: pointer;
    transition: transform ease-in .1s;
    &__back{
        top: 28px;
        left: 25px;
        z-index: 999;
        color: #fff;
        background-color: $lightblue;
        width: 57px;
        height: 57px;
    }
    &__icon{
      font-size:58px;
    }
  }